@font-face {
font-family: 'SEGA LOGO FONT';
font-style: normal;
font-weight: bolder;
src: local('SEGA LOGO FONT'), url('SEGA.woff') format('woff');
}

h1 {
  font-family: 'SEGA LOGO FONT';
  /*font-weight: 200;*/
  font-size: 50px;
  color: #0ccac4;
}

a{
  text-decoration: none
}


code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
